@use 'base/mixins' as m;
@use 'base/variables' as v;

.lineup {
    padding: 5rem 0;
    margin-top: 5rem;
    background-color: v.$rosa;

    h3 {
        text-align: center;
        color: v.$blanco;
        
    }

    p {
        color: v.$blanco;
        margin: 5rem 0;
        text-align: center;
        text-transform: uppercase;
    }
}

.bg-amarillo {
    background-color: v.$amarillo;

   li:nth-child(odd){
    background-color: darken(v.$amarillo, 15%);
   }
}

.Bg-azul {
    background-color: v.$verde;

    li:nth-child(odd){
        background-color: darken(v.$verde, 10%);
       }
}

.contenedor-lineup {
    @include m.tablet {
         @include m.grid(2, 2rem);
    }
}

.contenido-rock {
  p {
    margin: 0;
    padding-top: 2rem;
    color: v.$negro;
    text-transform: none;
    &:after {
        content: '';
        display: block;
        width: 20rem;
        height: 10rem;
        background-size: 15rem;
        background-position: center center;
        background-repeat: no-repeat;
        margin: 2rem auto 0 auto;
        background-image: url(../img/icono_rock.png);
    }
  }

  
}

.contenido-edm {
   
  p {    
    text-transform: none;
    color: v.$negro;
    padding-top: 2rem;
    margin: 0;
    &:after {
        content: '';
        display: block;
        width: 20rem;
        height: 10rem;
        background-size: 15rem;
        background-position: center center;
        background-repeat: no-repeat;
        margin: 2rem auto 0 auto;
        background-image: url(../img/icono_edm.png);
    }
  }
}

.artistas {
    margin: 0;
    padding: 0;
    list-style: none;

    li {
        padding: 2.5rem;
        font-weight: 700;
    }
    span{
        font-weight: 400;
    }
}

