@use 'base/mixins' as m;
@use 'base/variables' as v;

.video {
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;
    
    .overlay {
     position: absolute;
     height: 100%;
     width: 100%;
     background: rgb(138,43,90);
     background: linear-gradient(221deg, rgba(138,43,90,1) 0%, rgba(255,0,104,0.4374124649859944) 100%);
     display: flex;
     flex-direction: column;
     justify-content: center;
    }
    video {
        width: auto;
        display: block;
        @include m.desktop {
            width: 100%;     
        }

    }
}

.contenido-video {
    h2 {
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;
        z-index: 10;
    }

    p {
        color: v.$blanco;
        background-color: v.$rosa;
        font-weight: 700;
        display: inline-block;
        padding: 1rem 2rem;
        font-size: 2.4rem;
        margin: 0;
    }
}