@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    padding: 2rem;
    @include m.tablet {
        &.fijo {
            top: 0;
            left: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
        }
    }
    .contenido-header {
    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    }
    
    text-align: center;
    background-color: v.$verde;
    h1 {
        @include m.tablet {
             margin: 0;
        }
        color: v.$blanco;
    }
}

.navegacion-principal {
    @include m.tablet {
        display: flex;
        gap: 2rem;        
   }
    a { 
        @include m.tablet {
            margin: 0;
        }      

        display: block;
        text-align: center;
        margin-bottom: 2rem;
        color: v.$blanco;
        font-size: 2.4rem;
        
        &:hover {
            color: v.$amarillo;
        }
        
        &:last-of-type {
            margin: 0;
        }
    }

}
