@use 'base/mixins' as m;
@use 'base/variables' as v;

.seccion-festival {

    margin-top: 5rem;
    @include m.tablet {
        @include m.grid(2, 5rem);     
    }
}

.contendio-festival {
    margin-top: 5rem;
    @include m.tablet {
         margin: 0;
         h2 {
            margin-bottom: 3rem;
         }
    }
    
    .fecha {
      color: v.$rosa;
      font-weight: 700;
      margin-bottom: 5rem;
      @include m.tablet {
         margin-bottom: 3rem;
      }
    }
}

