@use 'base/mixins' as m;
@use 'base/variables' as v;

.boletos {
    padding-bottom: 5rem;
    h3{
        text-align: center;
        color: v.$blanco;
        padding: 5rem 0 0 0;
        margin: 0;
    }
}

.contenido-boletos {
    @include m.tablet {
        @include m.grid(2, 5rem);     
    }
}

.pase {
    &-titulo {
        margin: 3rem;
        text-align: center;
        color: v.$blanco;
        font-size: 2.8rem;
    }
    &-header {
        padding: 1rem 0;
        text-align: center;
        color: v.$blanco;
        text-transform: uppercase;
        background-color: v.$rosa;
        margin: 0;
    }
    &-lista {
        
        background-color: v.$blanco;
        
        ul {
            @include m.reset-lista;          
        
        li,
        :nth-child(odd){
            background-color: v.$blanco;    
            padding: 1rem;
            border-bottom: 1px solid #e1e1e1;
           }     
        }   
    }
    &-footer {
        background-color: v.$morado;
        padding: 1rem 0;
        text-align: center;
        color: v.$blanco;
        font-weight: 700;
        font-size: 2.5rem;
    }
}