@use 'base/variables' as v;

@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}

@mixin grid ($columna, $gap){
    display: grid;
    grid-template-columns: repeat($columna, 1fr);
    gap: $gap;
}

@mixin reset-lista{
    padding: 0;
    margin: 0;
    list-style: none;
}